/* Main container */
.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

/* Header styles */
.header {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f0f0f0;
  border-bottom: 1px solid #ddd;
}

.header-left {
  display: flex;
  align-items: center;
}

.header-button {
  padding: 5px 15px;
  background-color: #e0e0e0;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
}

/* Burger menu icon - hidden by default (desktop) */
.burger-menu {
  display: none; /* Hidden on desktop */
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 20px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  margin-right: 15px;
}

.burger-line {
  width: 100%;
  height: 3px;
  background-color: #333;
  border-radius: 3px;
}

/* Main content area */
.main-content {
  display: flex;
  flex: 1;
  overflow: hidden;
  position: relative;
}

/* Sidebar styles */
.sidebar {
  width: 200px;
  background-color: #f8f8f8;
  border-right: 1px solid #ddd;
  overflow-y: auto;
  z-index: 100;
}

.sidebar-header {
  display: none; /* Hidden on desktop */
  padding: 15px;
  border-bottom: 1px solid #eee;
  justify-content: space-between;
  align-items: center;
}

.sidebar-header h3 {
  margin: 0;
}

.close-sidebar {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.chat-item {
  padding: 15px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  transition: background-color 0.2s;
}

.chat-item:hover {
  background-color: #f0f0f0;
}

.chat-item.selected {
  background-color: #e6f7ff;
  border-left: 3px solid #1890ff;
}

/* Chat container styles */
.chat-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #e6ffe6; /* Light green background as shown in the image */
  position: relative;
  overflow: hidden;
}

/* Top bar styles */
.top-bar {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  flex-wrap: wrap;
  gap: 10px;
}

.assistant-buttons {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.assistant-button {
  padding: 5px 15px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap;
}

.db-select {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #d9e6ff; /* Light blue as shown in the image */
}

/* Audio controls */
.audio-controls {
  position: absolute;
  top: 80px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 10px;
  border-radius: 20px;
  z-index: 10;
}

.audio-button {
  width: 30px;
  height: 30px;
  border: none;
  background: transparent;
  color: white;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Chat box styles */
.chat-box {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.response-section {
  background-color: #d9d9ff; /* Light purple as shown in the image */
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 10px;
  max-width: 80%;
  word-break: break-word;
}

/* Query section styles */
.query-section {
  display: flex;
  padding: 10px;
  background-color: #d9e6ff; /* Light blue as shown in the image */
  border-top: 1px solid #ddd;
}

.query-section input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.query-section button {
  padding: 10px 20px;
  background-color: #1890ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap;
}

.query-section button:hover {
  background-color: #40a9ff;
}

/* Overlay for mobile sidebar */
.sidebar-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 90;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  /* Show burger menu ONLY on mobile */
  .burger-menu {
    display: flex;
  }
  
  /* Hide sidebar by default on mobile */
  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 250px;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
  }
  
  /* Show sidebar when open class is added */
  .sidebar.open {
    transform: translateX(0);
  }
  
  /* Show sidebar header on mobile */
  .sidebar-header {
    display: flex;
  }
  
  /* Show overlay when sidebar is open */
  .sidebar-overlay {
    display: block;
  }
  
  .top-bar {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .assistant-buttons {
    width: 100%;
    overflow-x: auto;
    padding-bottom: 5px;
  }
  
  .db-selector {
    width: 100%;
  }
  
  .db-select {
    width: 100%;
  }
  
  .response-section {
    max-width: 100%;
  }
  
  .query-section button {
    padding: 10px;
  }
}

/* Small mobile devices */
@media (max-width: 480px) {
  .header-button {
    padding: 5px 10px;
    font-size: 14px;
  }
  
  .audio-controls {
    top: 60px;
    padding: 8px;
  }
  
  .audio-button {
    width: 25px;
    height: 25px;
    font-size: 16px;
  }
  
  .chat-box {
    padding: 10px;
  }
  
  .response-section {
    padding: 10px;
  }
  
  .query-section input {
    padding: 8px;
  }
  
  .query-section button {
    padding: 8px;
  }
}